import {
  Component,
  OnInit,
  ViewChildren,
  QueryList,
  ElementRef
} from '@angular/core';
import { QuestionsService } from './questions.service';
import { Questions } from './questions';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { FeedbackService } from 'src/app/services/feedback/feedback.service';

@Component({
  selector: 'app-questions-page',
  templateUrl: './questions-page.component.html',
  styleUrls: ['./questions-page.component.scss']
})
export class QuestionsPageComponent implements OnInit {
  @ViewChildren('questionItem', { read: ElementRef })
  employeeCardElements: QueryList<ElementRef>;
  public itemsLength = 0;
  public hasNext: boolean = null;
  public hasPrev: boolean = null;

  questionList: Questions[] = [];

  constructor(
    private questionsService: QuestionsService,
    public feedbackService: FeedbackService,
    private sidebarService: SidebarService
  ) {
    this.sidebarService.handleSetSettings({showSidebar: false, bgColor: '#fff'});
  }

  toggleQuestion(question: HTMLElement, event) {
    event.stopPropagation();
    question.classList.toggle('is-expanded');
  }

  fetchQuestionList() {}

  public next() {}

  public prev() {}

  public start(canStart: boolean) {}

  private updatePagination() {
    this.hasNext = this.questionsService.hasNext();
    this.hasPrev = this.questionsService.hasPrev();
  }

  public getItemsLength() {}

  ngOnInit() {
    this.getItemsLength();
    this.fetchQuestionList();
    this.getItemsLength();
  }
}
