import { Injectable } from '@angular/core';
import { Questions } from './questions';
import * as moment from 'moment';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class QuestionsService {
  private listTitle = 'Вопросы и предложения';
  public questionList: Questions[] = [];

  // tslint:disable:no-inferrable-types
  private question: string = 'slQuestion';
  private answer: string = 'slAnswer';
  private order: string = 'slIndex';

  public pagedCollection: any;
  private pageSize: number = 15;
  private pageIndex: number = 0;

  public getQuestionList() {
    this.pageIndex = 0;
    return of(null);
  }

  public getQuestionListItems(list: any) {
    this.questionList = [];
    this.pagedCollection = list;
    const results: any[] = list.results;

    for (const item of results) {
      this.questionList.push({
        id: item.Id,
        question: item.slQuestion,
        answer: item.slAnswer,
        order: item.slIndexNumber,
        date: moment(item.Created).calendar()
      });
    }
  }

  public async getItemsLength() {
    return of(null);
  }

  public next() {
    return of(null);
  }

  public prev() {
    return of(null);
  }

  public hasNext(): boolean {
    return this.pagedCollection ? this.pagedCollection.hasNext : false;
  }

  public hasPrev(): boolean {
    return this.pageIndex > 0 ? true : false;
  }

  constructor() {
    moment.locale('ru');
    moment.updateLocale('ru', {
      calendar: {
        sameDay: '[Сегодня]',
        nextDay: 'DD MMMM YYYY',
        nextWeek: 'DD MMMM YYYY',
        lastDay: '[Вчера]',
        lastWeek: 'DD MMMM YYYY',
        sameElse: 'DD MMMM YYYY'
      }
    });
  }
}
